import React from 'react';
import { withRouter } from 'react-router-dom';
import {
  IconButton,
  Typography,
  Divider,
  Card,
  CardActionArea,
  CardContent,
} from '@material-ui/core';
import {ARCHIVE} from '../../analytics/analytics'
import HelmetConfig from '../../components/HelmetConfig/index'
import { SemipolarLoading } from 'react-loadingg';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import ArchiveIcon from '@material-ui/icons/Archive';
import { CSSTransition } from 'react-transition-group';

const rooturl = process.env.REACT_APP_ROOT_DOCUMENT_FOLDER;
const root = process.env.REACT_APP_ROOT_APIS;

class ArchivePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      inItems: false,
      isLoading: true,
    };
  }

  componentDidMount = () => {
    setTimeout(() => {
      this.fetchData();
    }, 500);
  };

  openURL = (url) => {
    setTimeout(() => {
      window.open(url, '_blank');
    }, 300);
  };

  fetchData = () => {
    fetch(`${root}/document.php?category=Archive`)
      .then((response) => {
        response
          .json()
          .then((res) => {
            // console.log(res);
            this.setState({ isLoading: false, data: res }, () => {
              this.setState({ inItems: true });
            });
          })
          .catch((err) => {
            console.log(err);
          });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  renderItems = () => {
    return this.state.data.map((val, ind) => {
     // let date = new Date('' + val.date).toDateString();
      let url = rooturl + val.document;
      return (
        <CSSTransition
          timeout={700}
          in={this.state.inItems}
          classNames={'documentTrans'}>
          <div className="docItems">
            <Card
              onClick={() => {
                this.openURL(url);
              }}
              style={{ width: '100%', height: '100%' }}>
              <CardActionArea style={{ width: '100%', height: '100%' }}>
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    width: '100%',
                    height: '55%',
                    backgroundColor: '#d32f2f',
                    color: 'white',
                  }}>
                  <ArchiveIcon style={{ fontSize: '100px' }} />
                </div>
                <CardContent
                  style={{
                    display: 'flex',
                    alignItems: 'flex-start',
                    justifyContent: 'space-between',
                    width: '88%',
                    flexDirection: 'column',
                    textAlign: 'start',
                    height: '35%',
                  }}>
                  <Typography
                    className="documentText"
                    variant="body2"
                    style={{
                      textAlign: 'start',
                      fontWeight: '600',
                      alignItems: 'center',
                    }}>
                    {val.name}
                  </Typography>
                  {/* <Typography
                    className="documentText"
                    variant="caption"
                    color="textSecondary"
                    style={{
                      textAlign: 'start',
                      fontWeight: '600',
                    }}>
                    {`Dated: ${date}`}
                  </Typography> */}
                </CardContent>
              </CardActionArea>
            </Card>
          </div>
        </CSSTransition>
      );
    });
  };

  render() {
    let noc = Math.floor(this.props.windowWidth / 200);
    noc = noc - 1;
    let x = '';
    for (let i = 0; i < noc; i++) {
      x = x + 'auto';
      if (i + 1 < noc) x = x + ' ';
    }

    return (
      <div
        style={{
          flexDirection: 'column',
          display: 'flex',
          minHeight: '92vh',
          alignItems: 'center',
          flex: '1',
        }}>
          <HelmetConfig data={ARCHIVE}/>
        <div
          style={{
            width: '100%',

            height: this.props.windowWidth > 1150 ? '150px' : '125px',
          }}></div>
        <div
          style={{
            width: '95%',
            padding: '15px',
            marginBottom: '10px',
            alignSelf: 'center',
            display: 'flex',
            textAlign: 'start',
            flexDirection: 'column',
          }}>
          <div
            style={{
              width: '95%',
              display: 'flex',
              textAlign: 'start',
              flexDirection: 'row',
              alignItems: 'center',
            }}>
            <IconButton
              color="primary"
              size="medium"
              onClick={() => {
                this.props.history.goBack();
              }}>
              <KeyboardBackspaceIcon
                color="primary"
                style={{ marginRight: '15px', fontSize: '50px' }}
              />
            </IconButton>
            <Typography
              color="primary"
              variant="h4"
              style={{ fontWeight: '600', marginLeft: '8px' }}>
              Archive
            </Typography>
          </div>
          <Divider
            style={{
              width: '100%',
              height: '1px',
              marginTop: '8px',
              backgroundColor: '#7b3f00',
            }}></Divider>
        </div>
        {this.state.isLoading ? (
          <div
            style={{
              width: '100%',

              height: '40vh',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }}>
            <SemipolarLoading
              color="#7b3f00"
              size="large"
              style={{
                position: 'relative',
              }}
            />
          </div>
        ) : this.state.data.length ? (
          <div
            style={{
              width: '95%',
              display: 'grid',
              gridTemplateColumns: x,
              justifyContent: noc <= 1 ? 'space-around' : 'space-between',
              padding: '8px',
              rowGap: '30px',
            }}>
            {this.renderItems()}
          </div>
        ) : (
              <div
                style={{
                  width: '100%',
                  height: '30vh',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                }}>
                <Typography variant="h5" color="textSecondary">
                  No data avaliable
            </Typography>
              </div>
            )}
      </div>
    );
  }
}

export default withRouter(ArchivePage);
