import React from 'react';
import { withRouter } from 'react-router-dom';
import {
  IconButton,
  Typography,
  Divider,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
} from '@material-ui/core';
import HelmetConfig from '../../components/HelmetConfig/index'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {ABOUT_PAGE} from '../../analytics/analytics'
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import { CSSTransition } from 'react-transition-group';
import './styles.css';

class AboutPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          headline: 'The first year after formation',
          text: [
            'The State Government recognized the Association in 1969. (G. O. No. 6586-Edn. (A) dt. 08/12/1969).',
            "The first election of the Association was held in 1968. At that time election was an annual affair. The first Executive Committee was constituted in 1969. Prof. Achintya Kumar Mukherjee and Prof. Ajit Kumar Mukherjee were elected the first President and General Secretary of West Bengal Government College Teachers' Association. The first movement launched by the Association was a silent mass rally brought out on April, 30, 1969 that culminated in handing over to the State Government a Charter of Demands. The major demands were: (1) merger of different pay scales for various categories of teachers on the basis of the principle of 'same work, same pay', (2) upgradation of the age of superannuation, (3) special allowance for teachers working in Government Colleges of North Bengal, (4) pending consideration of the memorandum of the Association to the Hazra Commission (the First Pay Commission of the State Government) regarding granting of monthly interim allowance of Rs. 100/-. The rally bore no placards. The first Annual Conference of the Association was held on 7-8 June, 1969 in the ABTA Hall. Prof. Janakiballav Bhattacharya presided over the first Annual Conference and the then Chief Minister, Shri Ajoy Kumar Mukhopadhyay was the Chief Guest.",
          ],
          list: false,
        },
        {
          headline: 'The days of turmoil',
          text: [
            'The Association was in organizational doldrums caused by legal complicacies arising out of challenging the election of 1970 in the court of law. The deadlock in organizational activities continued for three years. Merger of services (WBJES and WBES) was recommended by the First Pay Commission (popularly known as Hazra Commission). The Commission also recommended additional increment for experience to be counted towards fixation of pay. Seven Junior State Service members started movements for implementation of these recommendations. At such a juncture the teachers belonging to the WBJES felt the need for a separate Association. But good sense prevailed as every one realized that such bifurcation would fracture the solidarity of Government College teachers. After the Association was retrieved from the legal morass, the members decided to depute two veteran members for liaising with the Associations of six other junior services.',
          ],
          list: false,
        },

        {
          headline: 'Fight for "same work same pay"',
          text: [
            "UGC-recommended pay scales were first introduced in Government Colleges w. e. f. 1.1.1973. The Association had foreknowledge about non-coverage of certain categories of teachers of general Government Colleges. A special conference was convened on April 28, 1974. A comprehensive resolution was adopted. It was demanded that discriminatory principles so long pursued be done away with and integrated pay scales on the basis of the principle of 'same work, same pay' be introduced for the teacher-librarians of Government Colleges and Training Colleges and Technological Colleges and all categories of teachers with heterogeneous designations, viz. Librarians, Asst. Librarians, Physical Instructors, teachers of general subjects of Science and Arts working in technological colleges, Craft Instructors and Asst. Lecturers in Government Art College, teachers of pre-merger services in Chandannagar Government College, some teachers attached to Bureau of Psychological Research, teachers attached to B. E. College under the designation of 'Instructor', and Lecturer of Hindi in David Hare Training College. Inspite of assurances given by the Union Education Minister, Prof. Nurul Hassan, the Government order (No. 3467 Edn. (A) dated. 14.08.1976) bypassed the interest of various categories of Government College teachers as detailed above. The age of superannuation remained at 58 with conditional reemployment of two more years. Expressing deep resentment in the Special Conference in September, 1976 the Association launched various movement programmes. In the subsequent revision of UGC pay scales (1986, 1996) some lapses and lacunae surfaced under the Association's scanner. There was inordinate delay in the introduction of AICTE pay scales in the Technological Colleges. On 11 November, 1977 a delegation from the Association met the then Chief Minister Shri Jyoti Basu and pointed out the injustice meted out to Government College teachers through exclusion from the TOR (Terms of Reference) of the State Pay Commission headed by Prof. A. J. A. Taiab. The injustice was removed quickly and service matters of Government College teachers came under the Pay Committee TOR. Pay scale was already under the jurisdiction of the UGC.",
          ],
          list: false,
        },

        {
          headline: 'Getting a registered office',
          text: [
            'Without any office room the Association carried on its activities in the first decade of its formation (1967-1977). The personal residences of the General Secretaries were used as registered office of the Association. After much persuasion a room in Goenka College was allotted to the Association (G. O. No. 899-Edn (A) dtd. 17.04.1978). The office was shifted to Sanskrit College after twenty years. The Association owes its gratefulness for this facility to the State Education Department,Education Directorate and to the authorities of Goenka College of Commerce and Business Administration and Sanskrit College.',
          ],
          list: false,
        },
        {
          headline: 'The Constitution - its amendments',
          text: [
            'In the very first Annual General Conference (June 8, 1969) a resolution was adopted for asserting the objectives of the Association: "To infuse and inculcate a spirit of dedicatory idealism among the teachers of Government Colleges as would arouse them all to the performance of their duties with greater responsibility and sincerity". Later, this objective was enshrined in the Constitution of the Association [Part III, 5(G)]: "To strive for and ensure the development of a democratic bias in the control and administration of the Government Colleges in West Bengal and to secure, to that end, such delegation of powers to the Staff Councils of Government Colleges in West Bengal as should enable them to advise the Principals and higher authorities in matters related to the interest of the institutions or to the individual or collective interests of the teachers posted there ',
            'To give the Association a more participatory orientation the Constitution of the Association has been amended several times. Some provisos of greater democratic functioning have been added.',
          ],
          list: [
            'The initial proviso of nominating the three-man Election Commission by the President of the Association has been replaced by direct election in the Annual General Conference by the general members present. The number of members of the Election Commission now stands at five.',
            'In the twelfth Annual Conference in 1994 the members of the Election Commission were debarred from contesting for any position in the College Unit, General Council and Executive Council. This was done to ensure the distinct status of the Election Commission.',
            'The Annual Calendar of the Association extends from January 1 to December 31. Accordingly, the Annual Conference should be held by 31st December every year. The deadline was followed up to the 8th Annual Conference in 1980. Later, Part-VII, Article 7(A) (i) was revised to postpone the date of the Annual Conference up to the end of February on compulsive necessity.',
            'The term of a Committee was initially for a year, but was extended to two years through amendment of the Article 8(vii) of Part-VIII and Article 6E (xvi) of Part-VI. No functionary can occupy the same position consecutively for two terms, i.e., for four years.',
            'One position in the Executive Committee was reserved for a woman teacher. In the twelfth Annual General Meeting the principle of "single non-transferable proportional representation" was adopted for formation of Executive Committee by the members of the General Council. This principle widened the scope of \'minority voice\' in decision-making. Superannuated teachers were given scope to be "life members" of the Association.',
          ],
        },
        {
          headline: 'The Annual Conferences and "News Letters"',
          text: [
            'The Annual Conferences of the Association were held in rented halls for the first five years. The 6th Conference was held in Sanskrit College where the office of the Association is now being housed. Conferences from the 7th to the 38th ones were held in the Derozio Hall, Presidency College. . The 39th, 40th and 41st Conferences were held in the Hastings House Complex. Annual Conferences of the Association are also occasions for holding Book and Art Exhibitions, Inter-Unit Cricket matches, Blood Donation camps and extending free health check-up facilities. Seminars on contemporary issues are usually held on the first day of the Conference. The topics touch upon seminal social and educational issues of national and international significance.',
            'The organizational "News Letters" contain expositions on topical issues of higher learning discussed elaborately in the Executive Committee and General Council meetings, gist of seminars and workshops organized by the Association, accounts of the Association\'s regular activities and also includes all communications with the Government. The "News Letters" also make an attempt to publish important Government Orders relevant to service conditions of the Teachers of Government Colleges and thus making them available to all members of the Association. The "News Letters" also contain a summary of the activities of the All India Federation of University and College Teachers Organisation (AIFUCTO) and World Federation of Teachers\' Unions / Federacion Internacional Sindical De La Ensenanza (AFTU / FISE).',
          ],
          list: false,
        },
        {
          headline: 'Significant "movements" and their outcome',
          text: [],
          list: [
            'The Association organized prolonged movements for appropriate representation of Government College teachers in the various bodies of University administration. In the Calcutta University Act of 1981 representation of Government College teachers was admitted in the Calcutta University Senate, Syndicate, U. G. Councils/P. G. Councils, etc. Subsequently, in the Acts of all other State Universities such representations were guaranteed. Though a rather small fraction of the teaching community in the state, the Government College teachers have been performing significant role in the academic administration and examination systems of primary, secondary, higher secondary, college and university levels of education.',
            "The Association placed a demand for a girls' hostel for the girl students of Government Colleges. Only Lady Brabourne College had a girls' hostel then. The building at BF-142 at Salt Lake was vacated when Bidhannagar Government College was shifted to CF Block to its own building. The Association pressed for the proposed Girls' Hostel to be housed at BF-142.",
            'The Association demanded that every Government College must have the post of an Accounts Officer so that Principals of such colleges might concentrate on academic activities of their respective colleges.',
            'On July 22, 1984 the Association organized a Convention of training college teachers on the various service matters of the teachers, restructuring training courses and curricula, problems of student admission, formation of a State Board of Teacher Education (state counterpart of NCTE), etc.',
            "The 28th (2000) and 30th (2002) Annual General Conferences resolved to 'oppose granting of autonomous status to any college and urged the Government to review any kind of disparity in resource appropriation among various Government Colleges to ensure balanced development and infrastructural uplift of all Government Colleges. The Association also opposed appointment/engagement of 'contractual teachers', as this would have encouraged bureaucratic procrastination in filling up the vacant posts through regular P.S.C. nominees.",
            'The Association took appreciable initiatives in introducing the Bachelor of Visual Art degree in Government Art College after it was affiliated to Calcutta University in 1981. B.Sc. (Tech) course started at Ceramic Technology College in 1998 after it was affiliated through Association efforts.',
            'Association put up tough fight for extension of the benefits of UGC/AICTE scales for teachers of all four technological colleges in the state. Towards the end of 1980 the Association could impress upon the Hon\'ble Higher Education Minister to accept the recommendations of the Expert Committee for Government Technological Colleges formed under G.O. No. 7-Edn (A) dtd.2.1.1980 and 17 teachers were appointed to the upgraded posts of Lecturers. The TOR of the Expert Committee were inter alia "to study problems of teachers with anomalous designations performing teaching duties in 4 Technological Colleges." On March 14, 1982 a Convention was organized at Serampore Textile College to deliberate on introduction of P.G. courses in the Technological Colleges and setting up a "Development Council for Technological Colleges" for appropriate development and extension of these colleges. Calcutta University took significant initiatives towards introduction of PG courses by forming "Inspection Teams" for the Technological Colleges at the insistence of the Association.',
            'Prior to 1982 Librarians and Physical Instructors belonging to the same panel were posted both in Government Schools and Colleges. The Association could convince the Government to transfer 15 Librarians and 58 Physical Instructors from Government Schools to Government Colleges to enable all incumbents to be eligible for U.G.C. scales. Librarians were also given the benefit of superannuation at the age of 60 after they were given the same status as teachers. The G.O. No. 608-Edn (A) dtd.30.3.1982 reflected the sympathetic attitude of the then Government and the untiring persuasion by the Association.',
            "The Association threw all its weight for the regularization of 'Ad-hoc' teachers. The Association wanted \"regularization\" through the P.S.C., because the teachers of Government Colleges were in the 'Gazetted' ranks (now Group A). The Association did not want 'splitting of the Cadre in Gazetted and non-Gazetted services.' The problem was complicated in 1976 when the 1973-UGC scales were implemented. In the relevant G.O. the recruitment-qualification was upgraded. The number of ad-hoc teachers was above 100 when the first Left Front Government came to power. The Association demanded that the ad-hoc teachers should be given at least three chances to appear before the P.S.C. and that there should be no break of service. In the process most of the ad-hoc teachers could be inducted through the P.S.C. The remaining small numbers could be regularized through cabinet decision. A section of members gave the call of 'bypassing the P.S.C.' in regularizing the ad-hoc teachers. But in the Annual General Conferences the general members repeatedly disapproved the proposal. On this issue the Association suffered an organizational split. In spite of the fact that the Association could prevail upon the Government to clinch the issue, the split could never be mended. The Association still believes that the teachers belonging to the splinter group should return to the main fold of the Association to launch united and stronger movements against issues common to all teachers working in Government Colleges of the State.",
          ],
        },
        {
          headline: 'The Presidency episode',
          text: [
            'In a clandestine move, Presidency College has been severed from the West Bengal Educational Service, which marks the greatest ever harm to this service since its inception. This unprecedented move took the members of the service by surprise and they were quick to take to the streets under the leadership of the Association. No stone was left unturned to oppose the move but to no avail. The reason why transformation of Presidency College to a unitary university was opposed is that we believe that it was unwise to gamble with a 193-year old institution which has earned its repute catering primarily to undergraduate education under the affiliating system.',
            'Further, the experience of non-affiliating unitary mode in our country is far from satisfactory. Usually the first casualties in deemed or unitary universities are transparency, accountability and credibility. Our apprehensions are proving true.',
          ],
          list: false,
        },
        {
          headline: 'Our Demands',
          text: [
            "We reiterate our decade-old demand of forming a classified affiliating State University comprising all government colleges of the State, much on the lines of classified Universities like West Bengal University of Technology and West Bengal University of Health Sciences, a concept which has become all the more relevant today. This idea was initiated in the late nineties and our views were communicated to the State government time and again. We have also placed these demands along with a detailed roadmap before our Hon'ble Chief Minister and with the Minister in Charge of Higher Education hoping that one day our demand would be met and the move would facilitate the optimal utilization of the existing resources in the Government Colleges.",
          ],
          list: false,
        },
        {
          headline: 'The humanitarian face',
          text: [
            "The Association has always inspired its members to join hands with the welfare organizations. Ours is an Association which espouses 'broad-based' democratic movement. We never treat our Association as a mere tool for achieving service benefits and thereby reducing it to sheer economism. West Bengal Government College Teachers and Librarian Welfare Cooperative Society, an outfit of the Association, liberally contributed towards the treatment of ailing members. Our members have enthusiastically participated in literacy movements and organized a number of blood donation camps in our Annual Conferences to help the Thalasaemic Association. Our members have always shown their humanitarian face during times of calamity like floods or earthquakes.",
          ],
          list: false,
        },
        {
          headline: 'Participation in National/International issues',
          text: [
            'The Association has always been alive to the national and international issues. WBGCTA has always been vocal against neo-imperialist forces raising its ugly head time and again in various parts of the world. The Association has always maintained that wars cannot bring any lasting solution to any problem anywhere in the world. The Association has been consistent in opposing any war that may take place anywhere in the world and continuously urges and appeals to the conflicting parties to sit across the table and bring out solutions through fruitful dialogues and negotiations. The Association is never tired of appealing to peoples all over the world to abjure the path of violence and urges them to live in the spirit of amity and fraternity.',
            'Our Association expresses grave concern against the rise of religious fundamentalism and communalism in India and abroad. The Association has always maintained that Government should take the primary task of welfare activities on its shoulders and not allow private players to earn undue profit from rendering services that are essential in nature. The Association consistently protests against any attempt of privatization of welfare activities in the name of promoting liberalized economy. In its commitment to universal education the Association has always advocated (1) abolition of private ownership from education, (2) enhancement of financial allocation in higher education and (3) democratization of administration of educational institutions.',
          ],
          list: false,
        },
      ],

      genesisReadMore: false,
      inGenesis: false,
      inOthers: false,
    };
  }

  componentDidMount = () => {
    this.setState({ inGenesis: true, inOthers: true });
  };

  renderTypography = (text) => {
    return text.map((val, ind) => {
      return (
        <Typography paragraph variant="body2" style={{ textAlign: 'justify' }}>
          {val}
        </Typography>
      );
    });
  };

  renderList = (list) => {
    return list.map((val, ind) => {
      return (
        <li>
          <Typography
            paragraph
            variant="body2"
            style={{ textAlign: 'justify' }}>
            {val}
          </Typography>
        </li>
      );
    });
  };

  renderPanel = () => {
    return this.state.data.map((val, int) => (
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header">
          <Typography
            variant="h6"
            style={{ fontWeight: '600', textAlign: 'start' }}>
            {val.headline}
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails
          style={{
            disaply: 'flex',
            flexDirection: 'column',
            textAlign: 'justify',
          }}>
          {this.renderTypography(val.text)}
          {val.list ? (
            <ul style={{ listStyleType: 'cirlce' }}>
              {this.renderList(val.list)}
            </ul>
          ) : null}
        </ExpansionPanelDetails>
      </ExpansionPanel>
    ));
  };

  render() {
    return (
      <div
        style={{
          flexDirection: 'column',
          display: 'flex',
          minHeight: '92vh',
          alignItems: 'center',
          flex: '1',
        }}>
          <HelmetConfig data={ABOUT_PAGE}/>
        <div
          style={{
            width: '100%',
            height: this.props.windowWidth > 1150 ? '150px' : '125px',
          }}></div>
        <div
          style={{
            width: '95%',
            padding: '15px',
            marginBottom: '10px',
            alignSelf: 'center',
            display: 'flex',
            textAlign: 'start',
            flexDirection: 'column',
          }}>
          <div
            style={{
              width: '95%',
              display: 'flex',
              textAlign: 'start',
              flexDirection: 'row',
              alignItems: 'center',
            }}>
            <IconButton
              color="primary"
              onClick={() => {
                this.props.history.goBack();
              }}>
              <KeyboardBackspaceIcon
                color="primary"
                style={{ marginRight: '15px', fontSize: '50px' }}
              />
            </IconButton>
            <Typography
              color="primary"
              variant="h4"
              style={{ fontWeight: '600', marginLeft: '8px' }}>
              About Us
            </Typography>
          </div>
          <Divider
            style={{
              width: '100%',
              height: '1px',
              marginTop: '8px',
              backgroundColor: '#7b3f00',
            }}></Divider>
        </div>
        <div style={{ width: '90%', textAlign: 'start' }}>
          <CSSTransition
            timeout={700}
            in={this.state.inGenesis}
            classNames={'crouselInAnimation'}>
            <div style={{ width: '100%' }}>
              <Typography
                variant="h6"
                color="textPrimary"
                gutterBottom={true}
                style={{ fontWeight: '600' }}>
                The Genesis :
              </Typography>
              <Typography
                paragraph
                variant="body2"
                style={{ textAlign: 'justify' }}>
                The West Bengal Government College Teachers' Association
                (WBGCTA) is the pioneer organization in India comprising all
                categories of teachers belonging to a state service. Teachers of
                Government Colleges joining the State education services, felt
                over half a century back the need for an organizational platform
                to ensure to sustain proper educational environment in all
                Government Colleges, ensure better service conditions for the
                teachers of Government Colleges and encourage greater
                participation of teachers in governance of education, internal
                to such colleges. Encouraged by the declaration of The United
                Front Government of West Bengal (1967) that adequate importance
                would be given to the views of democratic organizations, eight
                veteran teachers of Government Colleges circulated a hand-out
                containing an appeal for formation of an Association of all
                Government College teachers. The so called "eight architects" of
                the West Bengal Government College Teachers' Association were
                (1) Prof. Sunil Kumar Lahiri (Convener), (2) Late Dr. Achinta
                Kumar Mukherjee, (3) Late Prof. Ramapada Bhattarcharya, (4) Late
                Prof. Ranjit Kumar Dasgupta, (5) Late Prof. Shibdas Ganguly, (6)
                Late Professor Prafulla Kumar Chakraborty, (7) Late Prof.
                Ramaprasad Das and (8) Late Prof. Ajit Kumar Mukherjee. Teachers
                of all Government Colleges felt enthused.
              </Typography>
            </div>
          </CSSTransition>
          {this.state.genesisReadMore ? (
            <Typography
              paragraph
              variant="body2"
              style={{ textAlign: 'justify' }}>
              Initially an adhoc committee was formed with four representatives/
              observers from each Government College. After long deliberations
              this committee drafted a constitution for the organization. In the
              ABTA hall at Ganesh Chandra Avenue the different provisos of the
              draft constitution were discussed threadbare. The issue that
              generated heated debate was whether all categories of Government
              College teachers (Senior Education Service, Education Service,
              Junior Education Service, and all teachers of non-gazetted status)
              could be organized under one banner without infringing the
              provisos of the WBSR and the Education Code. One opinion was that
              the Government could be sounded on this point. But the majority
              opted for an association launched in total disregard of any
              restriction, if there be any. Moreover, teachers present in the
              convention opined against inclusion of Principals as members of
              the proposed Association. Later in 1979 in the 7th Annual
              Conference held in the Muslim Institute the restrictive clause
              2(d) I Part II of the Constitution of the Association was deleted
              in order to extend membership to Principals of Government Colleges
              as well.
            </Typography>
          ) : (
              <Typography
                onClick={() => {
                  this.setState({ genesisReadMore: true });
                }}
                paragraph
                gutterBottom={true}
                style={{ cursor: 'pointer', fontWeight: '600' }}
                variant="body2"
                color="primary">
                Read More
              </Typography>
            )}
        </div>
        <CSSTransition
          timeout={700}
          in={this.state.inGenesis}
          classNames={'downTopAnimation'}>
          <div style={{ width: '92%', marginBottom: '30px' }}>
            {this.renderPanel()}
          </div>
        </CSSTransition>
      </div>
    );
  }
}

export default withRouter(AboutPage);
